:root {
  --raisin-black: rgba(24, 31, 41, 0.8);
  --raisin-black-transparent: rgba(24, 31, 41, 0.6);
  --space-cadet: rgb(28, 40, 75);
  --queen-blue: rgb(88, 113, 144);
  --queen-blue-transparent: rgba(88, 113, 144, 0.4);
  --beau-blue: rgb(184, 206, 223);
  --candy-pink: rgb(233, 96, 106);
  --candy-pink-transparent: rgba(233, 96, 106, 0.7);
  --candy-pink-light: rgb(233, 131, 138);
  /* =================================== */
  --baby-powder: rgb(250, 253, 246);
  --baby-powder-light: rgba(250, 253, 246, 0.4);
  --button-close: #6d0a02;
  --button-hover: #af1406;
}

body {
  margin: 0;
  height: 100vh;
  box-sizing: border-box;
  font-family: "Expletus Sans", cursive;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: url(/static/media/lightmode.cdfda11d.jpg);
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
  /* backdrop-filter: blur(5px); */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.is-dark-mode {
  --raisin-black: rgba(24, 31, 41, 0.8);
  --raisin-black-transparent: rgba(24, 31, 41, 0.8);
  --candy-pink-light: rgba(111, 228, 218, 0.849);
  --turquoise: rgba(111, 228, 218, 0.849);
  background-image: url(/static/media/darkmode.fad7cf50.jpg);
  background-repeat: no-repeat;
  background-attachment: fixed;
  transition: 2s;
  will-change: transform;
}

.is-light-mode {
  --baby-powder-light: rgba(250, 253, 246, 0.7);
  --raisin-black: rgba(24, 31, 41, 0.8);
  --raisin-black-transparent: rgba(24, 31, 41, 0.6);
  --space-cadet: rgb(28, 40, 75);
  --queen-blue: rgb(88, 113, 144, 0.2);
  --queen-blue-transparent: rgba(88, 113, 144, 0.253);
  --beau-blue: rgb(184, 206, 223);
  --candy-pink: rgb(233, 96, 106);
  --candy-pink-transparent: rgba(233, 96, 106, 0.7);
  --candy-pink-light: rgb(233, 131, 138);
  --turquoise: rgb(233, 96, 106);
  transition: 2s;
  will-change: transform;
}

